
.main-container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  border: solid 1px;
}

.trivia-container {
  border: solid 4px black;
  border-radius: 25px;
  width: 100vw;
  max-width: 800px;
  height: 75vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.question-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  width: 90%;
  max-width: 800px;
  margin: 0 auto;
  overflow-wrap: break-word;
}

.question {
  text-align: center;
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 2rem;
}

.btn-question {
  border: solid 2px black;
  border-radius: 25px;
  padding: 1rem;
  margin: 1rem;
  background-color: white;
  color: black;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
  margin: 0.5rem;
}

.btn-question:hover {
  background-color: black;
  color: white;
}

.answer {
  text-align: center;
  font-size: 1.5rem;
  font-weight: bold;
  max-width: 90%;
  margin: 0 auto;
  padding: 1rem;
}

.correct-answer {
  animation-name: flash;
  animation-duration: 1s;
  animation-iteration-count: 1;
}

@keyframes flash {
  0% {
    background-color: purple;
  }
  50% {
    background-color: white;
  }
  100% {
    background-color: purple;
  }
}


.btn-next {
  border: solid 2px black;
  border-radius: 25px;
  padding: 1rem;
  margin: 1rem auto 0;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
  display: block;
  background-color: purple;
  color: white;
}

@media only screen and (max-width: 768px) {
  .trivia-container {
    height: 100%;
    border: none;
  }
  
  .question {
    font-size: 1.5rem;
  }
  
  .answer {
    font-size: 1rem;
  }
  
  .btn-question {
    font-size: 0.8rem;
    padding: 0.5rem;
  }
  
  .btn-next {
    font-size: 0.8rem;
    padding: 0.5rem;
  }
}




